body {
	margin: 0;
	font-family: "Barlow Semi Condensed", sans-serif;
	background: radial-gradient(
		circle at top,
		hsl(214, 47%, 23%),
		hsl(237, 49%, 15%)
	);
	height: 100vh;
	color: white;
	letter-spacing: 1px;
}

h2 {
	font-size: 2.5rem;
	margin: 0;
}

.background {
	width: 80%;
	height: 100vh;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}

.header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	border: 3px solid hsl(217, 16%, 45%);
	border-radius: 5px;
	margin-top: 2em;
	margin-bottom: 2em;
	width: 100%;
}

.header-logo {
	max-width: 6em;
	margin-left: 1.5em;
}

.header-score-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	color: hsl(229, 25%, 31%);
	background-color: white;
	border-radius: 5px;
	margin: 0.5em;
	padding: 0.5em 1.5em;
}

.header-score-card > p {
	color: hsl(229, 64%, 46%);
}

.header-score-card > h2 {
	color: hsl(249, 11%, 37%);
}

.header-score-card > h2,
.header-score-card > p {
	margin: 0;
	padding: 0;
}

.main-content {
	/* display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column; */
	display: grid;
	grid-template-rows: 1fr 1fr;
	gap: 2em;
}

.end-message {
	text-align: center;
}

.end-message > h1 {
	font-size: 4rem;
	margin: 0;
}

.end-message > button {
	padding: 1em 4em;
	font-size: 1rem;
	border-radius: 10px;
	border: none;
	letter-spacing: 2px;
	font-family: "Barlow Semi Condensed", sans-serif;
	color: hsl(229, 25%, 31%);
	margin-top: 1em;
}

.rules {
	background-color: white;
	top: 0%;
	left: 0%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

/* Hands Styling */

.icon-container {
	background-image: url("./images/bg-triangle.svg");
	background-size: 50%;
	background-position: center;
	background-repeat: no-repeat;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	max-width: 300px;
	min-height: 300px;
}

.icon-rock,
.icon-paper,
.icon-scissors,
.icon-rock-rps,
.icon-paper-rps,
.icon-scissors-rps {
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: white;
	width: 100px;
	height: 100px;
	border-radius: 100px;
}
.icon-rock {
	border: 1em solid hsl(349, 71%, 52%);
	position: relative;
	left: 50%;
	transform: translateX(-50%);
}

.icon-rock-rps {
	border: 1em solid hsl(349, 71%, 52%);
}

.icon-paper,
.icon-paper-rps {
	border: 1em solid hsl(230, 89%, 62%);
}

.icon-scissors,
.icon-scissors-rps {
	border: 1em solid hsl(40, 84%, 53%);
}

.icon-rock:hover,
.icon-paper:hover,
.icon-scissors:hover {
	cursor: pointer;
	transform: scale(110%);
}

.icon-rock:hover {
	transform: scale(110%) translateX(-45%);
}

/* RPS Styling */

.RPS-container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr;
}

.RPS-choices {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.icon-house {
	background-color: hsl(220, 45%, 6%);
	width: 100px;
	height: 100px;
	border-radius: 100px;
}

/* Footer */

footer {
	border: 2px solid hsl(217, 16%, 45%);
	padding: 0 3em;
	border-radius: 10px;
	letter-spacing: 2px;
	margin-bottom: 4em;
}

footer:hover {
	cursor: pointer;
	transform: scale(110%);
}
